/* General App Styling */
.App {
  text-align: center;
  font-family: 'Rubik', Arial, sans-serif;
  background: linear-gradient(135deg, #667eea, #764ba2);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-x: hidden; /* Prevent horizontal scrolling */
  overflow-y: hidden; /* Prevent horizontal scrolling */
  padding-top: 60px; /* Add padding to account for the header height */
}

/* Header Styling */
.App-header {
  background: rgba(0, 0, 0, 0.5);
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  color: #fff;
  position: sticky;
  top: 0;
  z-index: 1000;
  width: 100%;
}

.App-header h1 {
  margin: 0;
  font-size: 2.5em;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

/* Responsive Design for Mobile Devices */
@media (max-width: 768px) {
  .App-header {
    padding: 10px;
  }

  .App-header h1 {
    font-size: 1.5em;
  }
}

@media (max-width: 480px) {
  .App-header h1 {
    font-size: 1.2em;
  }
}

/* Play Button Overlay */
.play-button-overlay {
  direction: rtl;
  position: absolute;
  top: 60px; /* Adjust this value as needed */
  left: 0;
  width: 100%;
  height: calc(100% - 60px); /* Adjust height accordingly */
  background: rgba(0, 0, 0, 0.7);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  z-index: 1;
}

.play-button-overlay button {
  padding: 12px 24px;
  font-size: 18px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background: #6cb7f5;
  color: white;
  transition: background 0.3s ease, transform 0.2s ease-in-out;
  font-family: 'Rubik', Arial, sans-serif;
}

.play-button-overlay button:hover {
  background: #9d5af5;
  transform: scale(1.05);
}

.play-button-overlay button:active {
  background: #1e7e34;
  transform: scale(1);
}

